body {
  box-sizing: border-box;
  font-family: "Montserrat", sans-serif;
  width: 100%;
}

h1,
h2,
h3,
h4 {
  margin: 0;
  font-weight: 300;
}

p {
  margin: 0;
}

a {
  text-decoration: none;
}

li {
  list-style: none;
}

.header {
  z-index: 4;
  width: 100%;
}

.container {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 2rem auto 0 auto;
  padding-bottom: 0;
  z-index: 4;
}

.title-animation {
  color: #ffffff;
  text-align: center;
  font-size: 1.2rem;
  z-index: 4;
  animation-name: title;
  animation-duration: 3s;
  width: 100%;
  font-weight: 300;
}
@media (min-width: 768px) {
  .title-animation {
    font-size: 1.5rem;
  }
}

@keyframes title {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
.animation__container {
  display: grid;
  width: 90%;
  margin: 1rem auto;
  justify-content: center;
  align-items: center;
}
@media (min-width: 768px) {
  .animation__container {
    grid-template-columns: repeat(2, 1fr);
  }
}
.animation__container .animation__flex {
  display: flex;
  flex-direction: column;
}
.animation__container .animation__lottie div {
  margin: 1rem auto;
}
@media (min-width: 768px) {
  .animation__container .animation__lottie div {
    width: 400px;
  }
}
@media (min-width: 990px) {
  .animation__container .animation__lottie div {
    width: 600px;
  }
}

.animation__div {
  display: flex;
  margin: 0 auto;
  align-items: center;
  justify-content: center;
}
@media (min-width: 768px) {
  .animation__div p {
    font-weight: 900;
    font-size: 3rem;
  }
}
@media (min-width: 1400px) {
  .animation__div p {
    font-size: 4rem;
  }
}
.animation__div .animation {
  height: 50px;
  overflow: hidden;
  margin-left: 1rem;
}
@media (min-width: 768px) {
  .animation__div .animation div {
    font-size: 1.3rem;
  }
}
@media (min-width: 1400px) {
  .animation__div .animation div {
    font-size: 2rem;
  }
}
.animation__div .animation > div > div {
  padding: 0.25rem 0.3rem;
  height: 2.81rem;
  margin-bottom: 2.81rem;
  display: inline-block;
}
@media (min-width: 768px) {
  .animation__div .animation > div > div {
    padding: 0.25rem 0.75rem;
  }
}
.animation__div .animation div:first-child {
  animation: text-animation 8s infinite;
}
.animation__div .first div {
  background-color: #4f45e5;
  color: #ffffff;
  display: flex;
  align-items: center;
  justify-content: center;
}
.animation__div .first div h2 {
  font-weight: 900;
  text-align: center;
}
.animation__div .second div {
  background-color: #1A4E9D;
  color: #ffffff;
  display: flex;
  align-items: center;
  justify-content: center;
}
.animation__div .second div h2 {
  font-weight: 900;
  text-align: center;
}
.animation__div .third div {
  background-color: #239B56;
  color: #ffffff;
  display: flex;
  align-items: center;
  justify-content: center;
}
.animation__div .third div h2 {
  font-weight: 900;
  text-align: center;
}
@keyframes text-animation {
  0% {
    margin-top: 0;
  }
  10% {
    margin-top: 0;
  }
  20% {
    margin-top: -5.62rem;
  }
  30% {
    margin-top: -5.62rem;
  }
  40% {
    margin-top: -11.24rem;
  }
  60% {
    margin-top: -11.24rem;
  }
  70% {
    margin-top: -5.62rem;
  }
  80% {
    margin-top: -5.62rem;
  }
  90% {
    margin-top: 0;
  }
  100% {
    margin-top: 0;
  }
}

p, h1, h2, h3, h4 {
  color: #ffffff;
}

.container__proyect {
  width: 100%;
  margin: 0 auto;
}
.container__proyect .title__proyect {
  text-align: center;
  color: #ffffff;
  margin: 3rem auto;
}
.container__proyect .images__proyect {
  display: flex;
}
@media (min-width: 768px) {
  .container__proyect {
    width: 80%;
  }
}

.proyect__animate {
  width: 100%;
  height: 100px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.about__container {
  width: 95%;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .about__container {
    width: 90%;
  }
}
@media (min-width: 990px) {
  .about__container {
    width: 80%;
  }
}
.about__container h1, .about__container p {
  color: #ffffff;
}
.about__container .about__grids {
  display: grid;
  column-gap: 1rem;
  margin-top: 0;
}
@media (min-width: 768px) {
  .about__container .about__grids {
    grid-template-columns: repeat(2, 1fr);
  }
}

.about__field {
  height: 400px;
  border: solid #ddd 1px;
  margin-top: 1rem;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}
.about__field .about__title {
  margin-bottom: 1rem;
  font-weight: 900;
}
.about__field .about__information {
  padding: 1rem;
}
.about__field .about__contact {
  margin-bottom: 2rem;
}
.about__field .about__user {
  width: 80px;
  margin-bottom: 2rem;
  border-radius: 50%;
}

.about__contacts {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 1rem;
}
@media (min-width: 768px) {
  .about__contacts {
    grid-template-columns: repeat(4, 1fr);
  }
}
.about__contacts .about__icon {
  width: 60px;
}
.about__contacts svg {
  fill: #ffffff;
}

.parallax {
  overflow: hidden;
  letter-spacing: -2px;
  line-height: 0.8;
  margin: 0;
  white-space: nowrap;
  display: flex;
  flex-wrap: nowrap;
}
.parallax .scroller {
  font-weight: 600;
  text-transform: uppercase;
  font-size: 64px;
  display: flex;
  white-space: nowrap;
  display: flex;
  flex-wrap: nowrap;
}
.parallax .scroller span {
  display: block;
  margin-right: 30px;
}

.tecno__container {
  width: 95%;
  margin: 1rem auto;
  position: relative;
}
@media (min-width: 768px) {
  .tecno__container {
    width: 80%;
  }
}
@media (min-width: 990px) {
  .tecno__container {
    width: 70%;
  }
}
.tecno__container p {
  text-align: center;
  margin: 2rem auto;
  font-weight: 300;
  font-size: 1.4rem;
}
.tecno__container .tecno__title {
  color: #ffffff;
  text-align: center;
  margin: 2rem auto;
  display: block;
  font-size: 1.5rem;
}

.tecno__grids {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 1rem;
}
@media (min-width: 768px) {
  .tecno__grids {
    grid-template-columns: repeat(4, 1fr);
  }
}
@media (min-width: 990px) {
  .tecno__grids {
    grid-template-columns: repeat(6, 1fr);
  }
}
@media (min-width: 1400px) {
  .tecno__grids {
    grid-template-columns: repeat(8, 1fr);
  }
}
.tecno__grids .tecno__field {
  display: flex;
  flex-direction: column;
  width: 100%;
  margin: 1rem auto;
  align-items: center;
  justify-content: center;
  width: 100px;
  border-radius: 50%;
  height: 90px;
  box-shadow: 0px 0px 14px 2px #1A4E9D;
  animation: image 3s ease-in-out infinite;
}
@keyframes image {
  0% {
    border-color: #1A4E9D;
    border-width: 1px;
    border-style: solid;
    box-shadow: 0px 0px 14px 2px #1A4E9D;
  }
  50% {
    border-color: #4f45e5;
    box-shadow: 0px 0px 14px 6px #4f45e5;
  }
  100% {
    border-color: #239B56;
    border-width: 1px;
    border-style: solid;
    box-shadow: 0px 0px 14px 2px #239B56;
  }
}
.tecno__grids .tecno__field p {
  width: min-content;
  display: block;
}
.tecno__grids .tecno__field .tecno__image {
  width: 45px;
  height: 45px;
  margin: 0 auto;
}

.proyect__grid {
  display: grid;
}
@media (min-width: 768px) {
  .proyect__grid {
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
  }
}

.proyect__card {
  display: flex;
  column-gap: 2rem;
  align-items: center;
  border: 1px solid #ffffff;
  padding: 1rem;
}
.proyect__card .proyect__image {
  flex: 1;
  width: 150px;
  height: 100%;
  object-fit: cover;
}
.proyect__card .proyect__info {
  flex: 3;
}
.proyect__card .proyect__info .proyect__paragraph {
  font-weight: 100;
  margin-top: 0.5rem;
}
@media (min-width: 768px) {
  .proyect__card .proyect__info .proyect__paragraph {
    display: -webkit-box;
    -webkit-box-orient: vertical;
    overflow: hidden;
    text-overflow: ellipsis;
    -webkit-line-clamp: 3;
  }
}
@media (min-width: 990px) {
  .proyect__card .proyect__info .proyect__paragraph {
    -webkit-line-clamp: 5;
  }
}
@media (min-width: 1400px) {
  .proyect__card .proyect__info .proyect__paragraph {
    -webkit-line-clamp: 10;
  }
}
.proyect__card .proyect__info .proyect__links {
  display: flex;
  justify-content: space-around;
  align-items: center;
  margin-top: 0.5rem;
}
.proyect__card .proyect__info .proyect__links .proyect__icon {
  width: 60px;
}